.modal-overlay {
    position: fixed; top: 0; left: 0; width: 100%; height: 100%; background: rgba(0, 0, 0, 0.4); display: flex;
    justify-content: center; align-items: center;    z-index: 1000; 
 }
  
  .modal-content {
     background: white; padding: 20px 30px; border-radius: 10px; text-align: center; box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
  }